:root {
  --color-black: hsl(0deg 0% 0%);
  --color-white: hsl(0deg 0% 100%);
  --neutral-000: hsl(0deg 0% 100%);
  --neutral-050: hsl(0deg 0% 98%);
  --neutral-100: hsl(0deg 0% 96%);
  --neutral-200: hsl(0deg 0% 90%);
  --neutral-300: hsl(0deg 0% 86%);
  --neutral-400: hsl(0deg 0% 83%);
  --neutral-500: hsl(0deg 0% 42%);
  --neutral-600: hsl(0deg 0% 32%);
  --neutral-700: hsl(0deg 0% 25%);
  --neutral-800: hsl(0deg 0% 15%);
  --neutral-900: hsl(232deg 53% 8%);
  --green-light: hsl(98deg 100% 98%);
  --green: hsl(98deg, 57%, 44%);
  --green-dark: hsl(98deg, 57%, 34%);
  --red-600: hsl(0deg 72% 51%);
  --orange-600: hsl(36deg, 100%, 50%);
  --font-sans:
    TTCommons, system-ui, BlinkMacSystemFont, "Segoe UI", "Roboto", "Ubuntu",
    "Droid Sans", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol";
  --font-serif: Georgia, "Times New Roman", Times, serif;
  --font-mono: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  --body-bg: var(--back-primary);
  --body-color: var(--front-primary);
  --font-base: var(--font-sans);
  --font-size-root: 100%;
  --font-size-base: 1rem;
  --font-weight-base: 400;
  --line-height-base: 1.25;
  --front-primary: var(--neutral-900);
  --front-secondary: var(--neutral-700);
  --front-tertiary: var(--neutral-500);
  --front-invert: var(--neutral-000);
  --front-accent: var(--green);
  --front-error: var(--red-600);
  --back-primary: var(--neutral-000);
  --back-secondary: var(--neutral-200);
  --back-tertiary: var(--neutral-300);
  --back-dark: var(--neutral-800);
  --back-accent: var(--green);
  --back-accent-light: var(--green-light);
  --back-gradient: linear-gradient(90deg, #24348b 0, #2f7d58 50%, #5eb12e 100%);
  --back-gradient-grey: linear-gradient(180deg, #fbfbfb 0, #fff 100%);
  --border-light: hsla(0deg, 0%, 100%, 0.4);
  --border-light-grey: var(--neutral-400);
  --border-grey: hsl(0deg, 0%, 94%);
  --border-accent: var(--green);
  --transition-fast: 0.125s ease;
  --transition-default: 0.25s ease;
  --transition-slow: 0.5s ease;
  --transition-slowest: 1s ease;
  --border-error: var(--red-600);
  --outline-color: var(--green);
  --z-index-negative: -1;
  --z-index-default: 1;
  --z-index-ring: 2;
  --z-index-hero-text: 4;
  --z-index-header: 5;
  --z-index-noscript: 99;
  --container-max-width: 1760px;
  --container-padding: 16px;
  --header-height: 104px;
}
@media screen and (min-width: 768px) {
  :root {
    --container-padding: 32px;
  }
}
@media screen and (min-width: 1280px) {
  :root {
    --container-padding: 80px;
  }
}

:root {
  font-size: var(--font-size-root);
}

html,
body {
  height: 100%;
}

html {
  min-width: 320px;
  min-height: 100%;
  font-size: var(--font-size-root);
  font-family: sans-serif;
  line-height: normal;
  -moz-osx-font-smoothing: inherit;
  -webkit-font-smoothing: inherit;
  -webkit-tap-highlight-color: rgb(var(--color-black) 0);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  color: var(--body-color);
  font-weight: var(--font-weight-base);
  font-size: var(--font-size-base);
  font-family: var(--font-base);
  line-height: var(--line-height-base);
  background-color: var(--body-bg);
}

svg {
  display: block;
}

noscript {
  position: absolute;
  top: 8px;
  right: 8px;
  left: 8px;
  z-index: var(--z-index-noscript);
  padding: 24px;
  text-align: center;
  background-color: var(--back-primary);
  border: 1px solid var(--border-error);
  border-radius: 8px;
}

[tabindex="-1"]:focus:not(:focus-visible) {
  outline: 0;
}

[hidden] {
  display: none;
}

@font-face {
  font-weight: 400;
