:root {
  --button-min-width: 96px;
  --button-display: inline-block;
  --button-padding: 16px 11px 14px;
  --button-line-height: 1;
  --button-border-radius: 25px;
  --button-outline-width: 4px;
  --button-state-active-scale: 0.92;
  --button-color-active: var(--front-invert);
  --button-color-disabled: var(--green);
  --button-border-color: var(--green);
  --button-border-hover-color: var(--green-dark);
  --button-border-active-color: var(--green);
  --button-border-disable-color: var(--green);
  --button-border-style: solid;
  --button-border-width: 1px;
  --button-outline-color: var(--emerald-500);
}

.button {
  display: var(--button-display);
  width: auto;
  margin: 0;
  padding: 0;
  overflow: visible;
  color: inherit;
  font: inherit;
  line-height: normal;
  background-color: transparent;
  border: none;
  border-radius: 0;
  outline-offset: 0;
  box-shadow: none;
  appearance: none;
  user-select: none;
  min-width: var(--button-min-width);
  padding: var(--button-padding);
  line-height: var(--button-line-height);
  text-decoration: none;
  border: var(--button-border-width) var(--button-border-style) var(--button-border-color);
  border-radius: var(--button-border-radius);
  outline-color: var(--button-outline-color);
  transition: background var(--transition-default), border var(--transition-default), color var(--transition-default), outline var(--transition-default), scale var(--transition-default);
  font-size: 18px;
  line-height: 1;
}
@media screen and (min-width: 768px) {
  .button {
    font-size: 24px;
  }
}
@media (hover: hover) {
  .button:hover:not(:active, :disabled) {
    border-color: var(--button-border-hover-color);
  }
}
.button:active:not(:disabled) {
  color: var(--button-color-active);
  border-color: var(--button-border-active-color);
  scale: var(--button-state-active-scale);
}
.button:focus-visible {
  outline-width: var(--button-outline-width);
  outline-style: solid;
}
.button:focus:not(:focus-visible) {
  outline-width: 0;
}
.button:focus:not(:-moz-focusring) {
  outline-width: 0;
}
.button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
.button[disabled] {
  color: var(--button-color-disabled);
  border-color: var(--button-border-disable-color);
  cursor: not-allowed;
  opacity: 0.4;
}
a.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

@media screen and (min-width: 768px) {
  .button {
    --button-min-width: 160px;
    --button-padding: 16px 30px 14px;
    --button-border-radius: 30px;
  }
}
.button[hidden] {
  display: none;
}
.button--primary {
  --button-primary-color: var(--front-invert);
  --button-primary-background: var(--green);
  --button-primary-background-hover: var(--green-dark);
  --button-outline-color: var(--green);
  color: var(--button-primary-color);
  background-color: var(--button-primary-background);
  border-color: var(--button-primary-background);
}
@media (hover: hover) {
  .button--primary:hover:not(:active, :disabled), .button--primary:active:not(:disabled) {
    color: var(--button-primary-color);
    background-color: var(--button-primary-background-hover);
    border-color: var(--button-primary-background-hover);
  }
}
.button--primary[disabled] {
  background-color: var(--neutral-800);
}
.button--secondary {
  --button-color-active: var(--green);
  --button-secondary-color: var(--green);
  --button-secondary-color-hover: var(--green-dark);
  --button-secondary-border-hover: var(--green-dark);
  color: var(--button-secondary-color);
}
@media (hover: hover) {
  .button--secondary:hover:not(:active, :disabled), .button--secondary:active:not(:disabled) {
    color: var(--button-secondary-color-hover);
    border-color: var(--button-secondary-border-hover);
  }
}

.calculator {
  padding: 60px 0 80px;
}
@media screen and (min-width: 1280px) {
  .calculator {
    padding: 80px 0 150px;
  }
}
.calculator__inner {
  width: min(100%, 1168px);
  margin: 0 auto;
}
.calculator__title {
  font-weight: 700;
  font-size: 32px;
  line-height: 1;
  margin: 0;
  text-align: center;
}
