  font-size: 24px;
  line-height: 1;
  letter-spacing: 0.24px;
  margin: 24px 0 0;
  text-align: center;
}
@media screen and (min-width: 992px) {
  .calculator__subtitle {
    font-size: 28px;
  }
}
@media screen and (min-width: 1280px) {
  .calculator__subtitle {
    margin-top: 34px;
    text-align: left;
  }
}
.calculator__descr {
  font-size: 18px;
  line-height: 1;
  margin: 8px 0 0;
  text-align: center;
}
@media screen and (min-width: 992px) {
  .calculator__descr {
    font-size: 24px;
  }
}
@media screen and (min-width: 1280px) {
  .calculator__descr {
    text-align: left;
  }
}

.container {
